import { FormRequestMakeup } from "../../../types";

type NameValues = {
    oldPassword: string;
    newPassword: string;
    confirmPassword: string;
};

const _formRequestAccountManagerAccountPassword: FormRequestMakeup<NameValues> = {
    validation: ({oldPassword,newPassword,confirmPassword}) => {
        if (newPassword===oldPassword) {
            return {
                message: "The new password must be different from the current password",
                inputNames: ["oldPassword","newPassword"]
            };
        }
        else if (newPassword!==confirmPassword) {
            return {
                message: "The new password and confirmation do not match",
                inputNames: ["newPassword","confirmPassword"]
            };
        }
    },
    inputs: {
        oldPassword: {
            caption: "Current password",
            required: true,
            validation: "min:6 | max:100"
        },
        newPassword: {
            caption: "New password",
            required: true,
            validation: "min:6 | max:100"
        },
        confirmPassword: {
            caption: "Confirm",
            required: true,
            validation: "min:6 | max:100"
        }
    },
    relativeUrl: "/users/account?action=edit-password",
};

export const formRequestAccountManagerAccountPassword = _formRequestAccountManagerAccountPassword as FormRequestMakeup;