import { Account } from "../../../hooks/use_account";
import { SmartState } from "../../../hooks/use_smart_state";
import { securePage } from "../../../components/common/secure_page";
import Form from "../../../components/common/form/form";
import Title from "../../../components/common/form/title";
import { formParentManageAccount } from "../manage-account";
import Box from "../../../components/common/form/box";
import P from "../../../components/common/form/p";
import Input from "../../../components/common/form/input";
import SubmitButton from "../../../components/common/form/submit-button";
import { formRequestAccountManagerAccountPassword } from "../../../lib/shared/form-request-makeup/requests/account/manage-account/password";

export type AccountManageAccountPasswordPageProps = {
    result: {};
    account: SmartState<Account | null>;
};

const AccountManageAccountPasswordPage = securePage(function({result,account}: AccountManageAccountPasswordPageProps) {
    const { user: { phone } } = account.value;

    return (<>
        <div className="account-manage-account-password-page">            
            <Form 
                requestMakeup={formRequestAccountManagerAccountPassword}
                parent={formParentManageAccount}
                autoFocus
                onSuccess={() => {
                    return {
                        type: "redirect",
                        path: "/account/manage-account",
                        message: {
                            type: "info",
                            message: "Your password has been updated."
                        }
                    };
                }}
            >
                <Title>Change your password</Title>
                <Box border>
                    <P>
                        For security reasons, to change your password we first need you to enter your current one. Then simply choose a new password and confirm it.
                    </P>
                    <Input name="oldPassword" type="password" />
                    <Input name="newPassword" type="password" />
                    <Input name="confirmPassword" type="password" />
                    <SubmitButton caption="Change" />
                </Box>
            </Form>
        </div>
        <style jsx>{`
            .account-manage-account-password-page {
                /* min-height: 600rem; */
                width: 100%;
            }
        `}</style>
    </>
);
});

export default AccountManageAccountPasswordPage;